<script>
// 数字，邮件，手机，url，身份证校验
import { isNumber,isIntNumer,isEmail,isPhone, isMobile,isURL,checkIdCard } from "@/utils/validate";
export default {
	data() {
		let self = this
		var validateIdCard = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!checkIdCard(value)) {
				callback(new Error("请输入正确的身份证号码"));
			} else {
				callback();
			}
		};
		var validateUrl = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isURL(value)) {
				callback(new Error("请输入正确的URL地址"));
			} else {
				callback();
			}
		};
		var validateMobile = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isMobile(value)) {
				callback(new Error("请输入正确的手机号码"));
			} else {
				callback();
			}
		};
		var validatePhone = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isPhone(value)) {
				callback(new Error("请输入正确的电话号码"));
			} else {
				callback();
			}
		};
		var validateEmail = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isEmail(value)) {
				callback(new Error("请输入正确的邮箱地址"));
			} else {
				callback();
			}
		};
		var validateNumber = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isNumber(value)) {
				callback(new Error("请输入数字"));
			} else {
				callback();
			}
		};
		var validateIntNumber = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isIntNumer(value)) {
				callback(new Error("请输入整数"));
			} else {
				callback();
			}
		};
		return {
			id: '',
			type: '',
			
			
			ro:{
				paotuidanhao : false,
				paotuimingcheng : false,
				paotuileixing : false,
				paotuidizhi : false,
				shoujiandizhi : false,
				choujin : false,
				jiedanzhuangtai : false,
				dingdanbeizhu : false,
				paotuitupian : false,
				fabushijian : false,
				yonghuzhanghao : false,
				yonghuxingming : false,
				clicktime : false,
				clicknum : false,
			},
			
			
			ruleForm: {
				paotuidanhao: this.getUUID(),
				paotuimingcheng: '',
				paotuileixing: '',
				paotuidizhi: '',
				shoujiandizhi: '',
				choujin: '',
				jiedanzhuangtai: '未接单',
				dingdanbeizhu: '',
				paotuitupian: '',
				fabushijian: '',
				yonghuzhanghao: '',
				yonghuxingming: '',
				clicktime: '',
			},
		
			paotuileixingOptions: [],
			jiedanzhuangtaiOptions: [],

			
			rules: {
				paotuidanhao: [
				],
				paotuimingcheng: [
				],
				paotuileixing: [
				],
				paotuidizhi: [
				],
				shoujiandizhi: [
				],
				choujin: [
					{ validator: validateIntNumber, trigger: 'blur' },
				],
				jiedanzhuangtai: [
				],
				dingdanbeizhu: [
				],
				paotuitupian: [
				],
				fabushijian: [
				],
				yonghuzhanghao: [
				],
				yonghuxingming: [
				],
				clicktime: [
				],
				clicknum: [
					{ validator: validateIntNumber, trigger: 'blur' },
				],
			}
		};
	},
	props: ["parent"],
	computed: {



	},
    components: {
    },
	created() {
		this.ruleForm.fabushijian = this.getCurDateTime()
	},
	methods: {
		
		// 下载
		download(file){
			window.open(`${file}`)
		},
		// 初始化
		init(id,type) {
			if (id) {
				this.id = id;
				this.type = type;
			}
			if(this.type=='info'||this.type=='else'){
				this.info(id);
			}else if(this.type=='logistics'){
				this.logistics=false;
				this.info(id);
			}else if(this.type=='cross'){
				var obj = this.$storage.getObj('crossObj');
				for (var o in obj){
						if(o=='paotuidanhao'){
							this.ruleForm.paotuidanhao = obj[o];
							this.ro.paotuidanhao = true;
							continue;
						}
						if(o=='paotuimingcheng'){
							this.ruleForm.paotuimingcheng = obj[o];
							this.ro.paotuimingcheng = true;
							continue;
						}
						if(o=='paotuileixing'){
							this.ruleForm.paotuileixing = obj[o];
							this.ro.paotuileixing = true;
							continue;
						}
						if(o=='paotuidizhi'){
							this.ruleForm.paotuidizhi = obj[o];
							this.ro.paotuidizhi = true;
							continue;
						}
						if(o=='shoujiandizhi'){
							this.ruleForm.shoujiandizhi = obj[o];
							this.ro.shoujiandizhi = true;
							continue;
						}
						if(o=='choujin'){
							this.ruleForm.choujin = obj[o];
							this.ro.choujin = true;
							continue;
						}
						if(o=='jiedanzhuangtai'){
							this.ruleForm.jiedanzhuangtai = obj[o];
							this.ro.jiedanzhuangtai = true;
							continue;
						}
						if(o=='dingdanbeizhu'){
							this.ruleForm.dingdanbeizhu = obj[o];
							this.ro.dingdanbeizhu = true;
							continue;
						}
						if(o=='paotuitupian'){
							this.ruleForm.paotuitupian = obj[o];
							this.ro.paotuitupian = true;
							continue;
						}
						if(o=='fabushijian'){
							this.ruleForm.fabushijian = obj[o];
							this.ro.fabushijian = true;
							continue;
						}
						if(o=='yonghuzhanghao'){
							this.ruleForm.yonghuzhanghao = obj[o];
							this.ro.yonghuzhanghao = true;
							continue;
						}
						if(o=='yonghuxingming'){
							this.ruleForm.yonghuxingming = obj[o];
							this.ro.yonghuxingming = true;
							continue;
						}
						if(o=='clicktime'){
							this.ruleForm.clicktime = obj[o];
							this.ro.clicktime = true;
							continue;
						}
						if(o=='clicknum'){
							this.ruleForm.clicknum = obj[o];
							this.ro.clicknum = true;
							continue;
						}
				}
				







				this.ruleForm.jiedanzhuangtai = '未接单'; 






			}
			
			// 获取用户信息
			this.$http({
				url: `${this.$storage.get('sessionTable')}/session`,
				method: "get"
			}).then(({ data }) => {
				if (data && data.code === 0) {
					
					var json = data.data;
					if(this.$storage.get("role")!="管理员") {
						this.ro.jiedanzhuangtai = true;
					}
					if(((json.yonghuzhanghao!=''&&json.yonghuzhanghao) || json.yonghuzhanghao==0) && this.$storage.get("role")!="管理员"){
						this.ruleForm.yonghuzhanghao = json.yonghuzhanghao
						this.ro.yonghuzhanghao = true;
					}
					if(((json.yonghuxingming!=''&&json.yonghuxingming) || json.yonghuxingming==0) && this.$storage.get("role")!="管理员"){
						this.ruleForm.yonghuxingming = json.yonghuxingming
						this.ro.yonghuxingming = true;
					}
				} else {
					this.$message.error(data.msg);
				}
			});
			
            this.$http({
				url: `option/paotuileixing/paotuileixing`,
				method: "get"
            }).then(({ data }) => {
				if (data && data.code === 0) {
					this.paotuileixingOptions = data.data;
				} else {
					this.$message.error(data.msg);
				}
            });
            this.jiedanzhuangtaiOptions = "已接单,未接单".split(',')
			
		},
    // 多级联动参数

    info(id) {
      this.$http({
        url: `paotuixiangmu/info/${id}`,
        method: "get"
      }).then(({ data }) => {
        if (data && data.code === 0) {
        this.ruleForm = data.data;
        //解决前台上传图片后台不显示的问题
        let reg=new RegExp('../../../upload','g')//g代表全部
        } else {
          this.$message.error(data.msg);
        }
      });
    },


    // 提交
    onSubmit() {
		if(this.ruleForm.paotuidanhao) {
			this.ruleForm.paotuidanhao = String(this.ruleForm.paotuidanhao)
		}









	if(this.ruleForm.paotuitupian!=null) {
		this.ruleForm.paotuitupian = this.ruleForm.paotuitupian.replace(new RegExp(this.$base.url,"g"),"");
	}






var objcross = this.$storage.getObj('crossObj');
      //更新跨表属性
       var crossuserid;
       var crossrefid;
       var crossoptnum;
       if(this.type=='cross'){
                var statusColumnName = this.$storage.get('statusColumnName');
                var statusColumnValue = this.$storage.get('statusColumnValue');
                if(statusColumnName!='') {
                        var obj = this.$storage.getObj('crossObj');
                       if(statusColumnName && !statusColumnName.startsWith("[")) {
                               for (var o in obj){
                                 if(o==statusColumnName){
                                   obj[o] = statusColumnValue;
                                 }
                               }
                               var table = this.$storage.get('crossTable');
                             this.$http({
                                 url: `${table}/update`,
                                 method: "post",
                                 data: obj
                               }).then(({ data }) => {});
                       } else {
                               crossuserid=this.$storage.get('userid');
                               crossrefid=obj['id'];
                               crossoptnum=this.$storage.get('statusColumnName');
                               crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
                        }
                }
        }
		this.$refs["ruleForm"].validate(valid => {
			if (valid) {
				if(crossrefid && crossuserid) {
					this.ruleForm.crossuserid = crossuserid;
					this.ruleForm.crossrefid = crossrefid;
					let params = { 
						page: 1, 
						limit: 10, 
						crossuserid:this.ruleForm.crossuserid,
						crossrefid:this.ruleForm.crossrefid,
					} 
				this.$http({ 
					url: "paotuixiangmu/page", 
					method: "get", 
					params: params 
				}).then(({ 
					data 
				}) => { 
					if (data && data.code === 0) { 
						if(data.data.total>=crossoptnum) {
							this.$message.error(this.$storage.get('tips'));
							return false;
						} else {
							this.$http({
								url: `paotuixiangmu/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "操作成功",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.paotuixiangmuCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});

						}
					} else { 
				} 
			});
		} else {
			this.$http({
				url: `paotuixiangmu/${!this.ruleForm.id ? "save" : "update"}`,
				method: "post",
			   data: this.ruleForm
			}).then(({ data }) => {
				if (data && data.code === 0) {
					this.$message({
						message: "操作成功",
						type: "success",
						duration: 1500,
						onClose: () => {
							this.parent.showFlag = true;
							this.parent.addOrUpdateFlag = false;
							this.parent.paotuixiangmuCrossAddOrUpdateFlag = false;
							this.parent.search();
							this.parent.contentStyleChange();
						}
					});
				} else {
					this.$message.error(data.msg);
			   }
			});
		 }
         }
       });
    },
    // 获取uuid
    getUUID () {
      return new Date().getTime();
    },
    // 返回
    back() {
      this.parent.showFlag = true;
      this.parent.addOrUpdateFlag = false;
      this.parent.paotuixiangmuCrossAddOrUpdateFlag = false;
      this.parent.contentStyleChange();
    },
    paotuitupianUploadChange(fileUrls) {
	    this.ruleForm.paotuitupian = fileUrls;
    },
  }
};
</script>
