import Vue from 'vue';
//配置路由
import VueRouter from 'vue-router'
Vue.use(VueRouter);
//1.创建组件
import Index from '@/views/index'
import Home from '@/views/home'
import Login from '@/views/login'
import NotFound from '@/views/404'
import UpdatePassword from '@/views/update-password'
import pay from '@/views/pay'
import register from '@/views/register'
import center from '@/views/center'
    import news from '@/views/modules/news/list'
    import jiaoliujilu from '@/views/modules/jiaoliujilu/list'
    import yonghu from '@/views/modules/yonghu/list'
    import dingdanqueren from '@/views/modules/dingdanqueren/list'
    import peisongjilu from '@/views/modules/peisongjilu/list'
    import config from '@/views/modules/config/list'
    import paotuiyuan from '@/views/modules/paotuiyuan/list'
    import paotuixiangmu from '@/views/modules/paotuixiangmu/list'
    import paotuijiedan from '@/views/modules/paotuijiedan/list'
    import paotuileixing from '@/views/modules/paotuileixing/list'
    import newstype from '@/views/modules/newstype/list'


//2.配置路由   注意：名字
export const routes = [{
    path: '/',
    name: '系统首页',
    component: Index,
    children: [{
      // 这里不设置值，是把main作为默认页面
      path: '/',
      name: '系统首页',
      component: Home,
      meta: {icon:'', title:'center', affix: true}
    }, {
      path: '/updatePassword',
      name: '修改密码',
      component: UpdatePassword,
      meta: {icon:'', title:'updatePassword'}
    }, {
      path: '/pay',
      name: '支付',
      component: pay,
      meta: {icon:'', title:'pay'}
    }, {
      path: '/center',
      name: '个人信息',
      component: center,
      meta: {icon:'', title:'center'}
    }
      ,{
	path: '/news',
        name: '公告信息',
        component: news
      }
      ,{
	path: '/jiaoliujilu',
        name: '交流记录',
        component: jiaoliujilu
      }
      ,{
	path: '/yonghu',
        name: '用户',
        component: yonghu
      }
      ,{
	path: '/dingdanqueren',
        name: '订单确认',
        component: dingdanqueren
      }
      ,{
	path: '/peisongjilu',
        name: '配送记录',
        component: peisongjilu
      }
      ,{
	path: '/config',
        name: '轮播图管理',
        component: config
      }
      ,{
	path: '/paotuiyuan',
        name: '跑腿员',
        component: paotuiyuan
      }
      ,{
	path: '/paotuixiangmu',
        name: '跑腿项目',
        component: paotuixiangmu
      }
      ,{
	path: '/paotuijiedan',
        name: '跑腿接单',
        component: paotuijiedan
      }
      ,{
	path: '/paotuileixing',
        name: '跑腿类型',
        component: paotuileixing
      }
      ,{
	path: '/newstype',
        name: '公告信息分类',
        component: newstype
      }
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {icon:'', title:'login'}
  },
  {
    path: '/register',
    name: 'register',
    component: register,
    meta: {icon:'', title:'register'}
  },
  {
    path: '*',
    component: NotFound
  }
]
//3.实例化VueRouter  注意：名字
const router = new VueRouter({
  mode: 'hash',
  /*hash模式改为history*/
  routes // （缩写）相当于 routes: routes
})
const originalPush = VueRouter.prototype.push
//修改原型对象中的push方法
VueRouter.prototype.push = function push(location) {
   return originalPush.call(this, location).catch(err => err)
}
export default router;
